.container{
    @include container();
}

.row{
    @include row(0);
}
.header{
    &__fon{
        width: 100vw;
        height: 100vh;
        background: rgb(0, 0, 0, 0.30);
        z-index: 999;
        position: absolute;
        display: none;
        transition: 0.8s;
    }
    &__prenav {
        @extend .container;
        @extend .row;
        justify-content: space-between;
        align-items: center;
        padding: 20px 0;
        &-lang{
            font-size: 18px;
            line-height: 13px;
            font-weight: 400;
            color: $greyDark;
            text-transform: uppercase;
        }
        &-title{
            font-size: 32px;
            line-height: 23px;
            font-weight: 700;
            text-transform: uppercase;
            color: $greyDark;
        }
        &-search{
            font-size: 20px;
            color: $black;
        }
    }
    &__nav{
        &-container{
            @extend .container;
            @extend .row;
            justify-content: space-between;
            align-items: center;
            border-top: 1px solid #c4c4c4;
            border-bottom: 1px solid #c4c4c4;
        }
        &-list{
            display: flex;
            column-gap: 50px;
            &.active{
                display: none;
            }
        }
        &-link{
            padding: 20px 0;
            display: block;
            color: $greyDark;
            font-weight: 400;
            font-size: 16px;
            line-height: 20px;
        }
        &-icons{
            display: flex;
            column-gap: 30px;
            padding: 20px 0;
        }
    }
}

@media (max-width: $md){
    .icon-menu{
        display: block;
    }
    .header__nav-list{
        display: none;
        &.active{
            position: absolute;
            top: -100%;
            left: 0;
            transition: 0.8s;
            padding-top: 100px;
            display: flex;
            flex-direction: column;
            text-align: center;
            width: 300px;
            background-color: $bg;
            z-index: 9999;
            height: 100vh;
            overflow: hidden;
        }    
    }
    }

.icon-menu{
    display: none;
}