*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    &::before,
    &::after {
        font-family: 'FontAwesome';
    }
}
a{
    text-decoration: none;
}

ul{
    list-style: none;
}

body{
    font-family: 'Montserrat';
    background: $bg;
}

.icon{
    &-search::before{
        content: '\f002';
        font-weight: 401;
    }
}
.icon{
    &-heart::before{
        content: '\f004';
        font-weight: 300;
    }
}
.icon{
    &-cart::before{
        content: '\f07a';
        font-weight: 300;
    }
}
.icon{
    &-user::before{
        content: '\f007';
        font-weight: 300;
    }
}
.icon{
    &-menu::before{
        content: '\f0c9';
        font-weight: 401;
    }
}