$grey:#E1E1E1;
$bg: #E5E5E5;
$black: #000000;
$greyDark: #323232;
$white: #FFFFFF;

$Montserrat: ('Regular',400),
             ('Bold',700);

@each $name, $weight in $Montserrat {
    @font-face {
        font-family: 'Montserrat';
        src: url(../fonts/Montserrat/Montserrat-#{$name}.ttf);
        font-weight: $weight;
    }    
}

$FontAwesome: ('brands-400',400),
              ('light-300',300),
              ('duotone-900',900),
              ('regular-400',401),
              ('solid-900',901);

@each $name, $weight in $FontAwesome {
    @font-face {
        font-family: 'FontAwesome';
        src: url(../fonts/webfonts/fa-#{$name}.ttf);
        font-weight: $weight;
    }
}
